// import fortawesome from '@fortawesome/fontawesome-free';
// import Swiper from 'swiper';
import Swiper from 'swiper/bundle';

import $ from 'jquery';
import jQuery from 'jquery';



$(document).ready(function() {
    class BeforeAfterSlider {
        constructor(container) {
            this.container = $(container);
            this.beforeImage = this.container.find('.image-before');
            this.sliderHandle = this.container.find('.slider-handle');
            this.isDragging = false;

            this.init();
        }

        init() {
            this.bindEvents();
            this.setInitialPosition();
        }

        bindEvents() {
            //     
            this.container.on('mousedown', this.startDrag.bind(this));
            this.sliderHandle.on('mousedown', this.startDrag.bind(this));

            //     -  
            this.sliderHandle.on('mousedown touchstart', function(e) {
                e.stopPropagation();
            });
        }

        startDrag(e) {
            e.preventDefault();
            this.isDragging = true;
            this.container.addClass('dragging');

            //   
            $('body').css({
                'user-select': 'none',
                'cursor': 'col-resize'
            });

            //    document   
            $(document).on('mousemove.beforeAfter', this.onDrag.bind(this));
            $(document).on('mouseup.beforeAfter', this.stopDrag.bind(this));

            //     
            $(document).on('touchmove.beforeAfter', this.onDrag.bind(this));
            $(document).on('touchend.beforeAfter', this.stopDrag.bind(this));
        }

        onDrag(e) {
            if (!this.isDragging) return;

            e.preventDefault();
            const containerRect = this.container[0].getBoundingClientRect();
            let clientX;

            if (e.type === 'touchmove') {
                clientX = e.originalEvent.touches[0].clientX;
            } else {
                clientX = e.clientX;
            }

            //    
            const x = clientX - containerRect.left;
            const percentage = (x / containerRect.width) * 100;

            //    0-100%
            const clampedPercentage = Math.max(0, Math.min(100, percentage));

            this.updateSliderPosition(clampedPercentage);
        }

        stopDrag() {
            if (!this.isDragging) return;

            this.isDragging = false;
            this.container.removeClass('dragging');

            //   
            $('body').css({
                'user-select': '',
                'cursor': ''
            });

            //  
            $(document).off('.beforeAfter');
        }

        updateSliderPosition(percentage) {
            //    ""
            this.beforeImage.css('width', percentage + '%');

            //   
            this.sliderHandle.css('left', percentage + '%');
        }

        setInitialPosition() {
            this.updateSliderPosition(50); //   50%
        }

        //    
        showBefore() {
            this.updateSliderPosition(100);
        }

        showAfter() {
            this.updateSliderPosition(0);
        }

        reset() {
            this.updateSliderPosition(50);
        }
    }

    //     
    const sliders = [];

    $('.before-after').each(function(index) {
        const slider = new BeforeAfterSlider(this);
        sliders.push(slider);

        //  data-  
        $(this).attr('data-slider-id', index);
    });

    //     ( )
    $(document).on('mouseup touchend', function() {
        $('body').css({
            'user-select': '',
            'cursor': ''
        });
        $(document).off('.beforeAfter');
    });

    //    ( )
    $('.control-btn').on('click', function() {
        const action = $(this).data('action');
        const sliderId = $(this).data('slider') || 0; //    

        if (sliders[sliderId]) {
            switch(action) {
                case 'before':
                    sliders[sliderId].showBefore();
                    break;
                case 'after':
                    sliders[sliderId].showAfter();
                    break;
                case 'reset':
                    sliders[sliderId].reset();
                    break;
            }
        }
    });

    //     
    $('.slider-controls .control-btn').on('click', function() {
        const $parent = $(this).closest('.before-after-container');
        const sliderId = $parent.index('.before-after-container');
        const action = $(this).data('action');

        if (sliders[sliderId]) {
            switch(action) {
                case 'before':
                    sliders[sliderId].showBefore();
                    break;
                case 'after':
                    sliders[sliderId].showAfter();
                    break;
                case 'reset':
                    sliders[sliderId].reset();
                    break;
            }
        }
    });
});









$(document).ready(function() {
    //  Swiper
    const videoSwiper = new Swiper('.video-slider', {
        slidesPerView: 1,
        spaceBetween: 20,
        loop: true,
        speed: 600,

        // 
        pagination: {
            el: '.swiper-pagination',
            clickable: true,
            dynamicBullets: true
        },

        // 
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },

        // 
        breakpoints: {
            //  (200-768)
            200: {
                slidesPerView: 1,
                spaceBetween: 10
            },
            //  (769-1280)
            769: {
                slidesPerView: 2,
                spaceBetween: 20
            },
            //  (1281+)
            1281: {
                slidesPerView: 4,
                spaceBetween: 30
            }
        },

        // 
        effect: 'slide',
        grabCursor: true,

        // 
        autoHeight: false,

        //  
        preloadImages: true,
        updateOnImagesReady: true
    });

    //    
    $('.video-link').on('click', function(e) {
        e.preventDefault();

        const videoUrl = $(this).attr('href');
        const videoId = $(this).data('video-id');

        //     
        $('#videoPlayer').html(
            `<iframe src="${videoUrl}?autoplay=1&rel=0" 
                    allow="autoplay; encrypted-media" 
                    allowfullscreen></iframe>`
        );

        //   
        $('#videoModal').fadeIn(300);

        //      
        videoSwiper.autoplay.stop();
    });

    //   
    $('.close-modal').on('click', function() {
        closeVideoModal();
    });

    //     
    $(window).on('click', function(e) {
        if ($(e.target).is('#videoModal')) {
            closeVideoModal();
        }
    });

    //   ESC
    $(document).on('keyup', function(e) {
        if (e.key === 'Escape') {
            closeVideoModal();
        }
    });

    function closeVideoModal() {
        $('#videoModal').fadeOut(300);
        $('#videoPlayer').empty();

        //  
        videoSwiper.autoplay.start();
    }

    //   -  Swiper
    let resizeTimer;
    $(window).on('resize', function() {
        clearTimeout(resizeTimer);
        resizeTimer = setTimeout(function() {
            videoSwiper.update();
        }, 250);
    });
});



